import { ValidationOptions, registerDecorator } from "class-validator";
import { RelationExistsRule } from "./relation-exists.rule";

export function RelationExists(model: any, validationOptions?: ValidationOptions) {
  return function (object: any, propertyName: string) {
    registerDecorator({
      name: 'RelationExists',
      target: object.constructor,
      propertyName: propertyName,
      constraints: [model],
      options: validationOptions,
      validator: RelationExistsRule,
    });
  };
}